.nav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;



  &__link {
    display: block;
    padding: 8px;
    //
    text-decoration: none;
    color: inherit;
  }

  &__item {
    float: left;
    //
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  } 

  &__expanded {
    cursor: pointer;
    &_content {
      padding: 0 18px;
      display: block;
      overflow: hidden;
      background-color: #f1f1f1;
    }
  }
}
